@font-face {
  font-family: "Choplin";
  src: url("../fonts/Rene Bieder - Choplin Medium-DEMO.otf") format("opentype");
}

@font-face {
  font-family: "Choplin-light";
  src: url("../fonts/32AAC6_0_0.woff") format("woff");
}

.landingPageIntro {
  font-family: Arial;
  color: #cecece;
}

.title {
  font-family: Choplin;
  color: #ffffff;
}

.sub-title {
  font-family: Choplin-light;
}

.info-text {
  font-family: Arial;
}

.overlay-title {
  font-family: Choplin;
  font-size: 40px;
  color: #e27500;
}

.faq-customer-information {
  margin-top: 55px;
  color: #535353;
  font-size: 14px;
  font-family: Arial;
  margin-bottom: 40px;
}

.faq-customer-information p {
  margin: 0;
}

.faq-h {
  font-size: 20px;
  font-family: Arial;
  margin: 0;
  font-weight: bold;
}

.faq-p {
  font-size: 20px;
  font-family: Arial;
  font-size: 14px;
  margin-left: 20px;
}

.cookie-title {
  font-family: Choplin;
  font-size: 40px;
  color: #e27500;
  margin-bottom: 0px;
  margin-top: 40px;
}

.cookie-info {
  line-height: 20px;
  font-family: Arial;
  font-size: 15px;
  color: #454545;
}

.filterButton {
  font-family: Arial;
  font-size: 30px;
}

.search-info {
  font-family: Arial;
  color: #535353;
  font-size: 16px;
}

.category p, .price-range p {
  font-family: Arial;
  color: #2b2b2b;
  font-size: 20px;
  margin-bottom: 10px;
}

.price-range p {
  margin-bottom: 20px;
}

.about-us-tag span {
  font-family: Choplin-light;
  color: #ffffff;
  font-size: 23px;
  margin-right: 10px;
  margin-top: 15px;
}

.bread-crum-tag {
  height: 14px;
  white-space: nowrap;
  position: relative;
  margin: 0 5px 0 10px;
  displaY: inline-block;
  height: 25px;
  border-radius: 0 2px 2px 0;
  padding: 0 25px 0 15px;
  background: #323232;
  border-top-width: 1px;
  border-bottom-width: 1px;
  color: #cecece;
  line-height: 23px;
  font-size: 14px;
}

.bread-crum-tag:before {
  position: absolute;
  content: "";
  color: white;
  text-shadow: 0 0 1px #323232;
  font-size: 11px;
  line-height: 0px;
  text-indent: 12px;
  left: -15px;
  width: 1px;
  height: 0px;
  border-right: 14px solid #323232;
  border-top: 12.5px solid transparent;
  border-bottom: 12.5px solid transparent;
}

.about-info, .contact-info {
  color: #ffffff;
  font-size: 14px;
  font-family: Arial;
}

.about-info p:first-child, .contact-info p:first-child {
  font-size: 20px;
  font-family: Choplin-light;
}

.about-info ul, .contact-info ul {
  padding-left: 20px;
}

.contact-form label {
  margin-top: 17px;
  margin-bottom: 17px;
  font-family: Arial;
  color: #ffffff;
}

.subtitle {
  color: #ffffff;
  font-size: 20px;
  font-family: Choplin-light;
}
